import wx from 'weixin-js-sdk'
import * as http from '../utils/http'
import sha1 from 'sha1'

const wxApi = {
    /**
     * [wxRegister Api]
     * @param  {Function} callback [ready]
     */
    wxRegister(callback) {
        // 
        http.fetch('/dhr/wechat/jsapi/ticket', {
            reqUrl: window.location.href.split('#')[0]
        }).then((res) => {
            // PS: 
            let noncestr = 'Wm3WZYTPz0wzccnW',
                timestamp = new Date().getTime(),
                jsapi_ticket = res.Result.ticket,
                url = location.href.split('#')[0]
            console.log(res)
            console.log(url)
            let string1 = `jsapi_ticket=${jsapi_ticket}&noncestr=${noncestr}&timestamp=${timestamp}&url=${url}`

            let signature = sha1(string1)
            console.log(signature)
            wx.config({
                debug: false, // 
                appId: res.Result.AppID, // 
                timestamp: timestamp, // 
                nonceStr: noncestr, // 
                signature: signature, // 1
                jsApiList: [
                    'onMenuShareTimeline',
                    'updateAppMessageShareData',
                    'updateTimelineShareData',
                    'onMenuShareAppMessage'
                ] // JSJS2
            })
        }).catch((error) => {
            console.log(error)
        })

        console.log(2)

        wx.ready((res) => {
            // ready
            console.log(res)
            if (callback) {
                callback()
            }
        })


        wx.error(res => {
            console.log('' + res)
        })
    },

    wxReady(callback) {
        wx.ready((res) => {
            // ready
            console.log(res)
            if (callback) {
                callback()
            }
        })
    },


    /**
     * [ShareTimeline ]
     * @param {[type]} option []
     * @param {[type]} success []
     * @param {[type]} error   []
     */
    ShareTimeline(option) {
        console.log(option)
        wx.onMenuShareTimeline({
            title: option.title, // 
            link: option.link, // 
            imgUrl: option.imgUrl, // 
            success() {
                // 
                option.success()
            },
            cancel() {
                // 
                option.error()
            }
        })
    },
    /**
     * [ShareAppMessage ]
     * @param {[type]} option []
     * @param {[type]} success []
     * @param {[type]} error   []
     */
    ShareAppMessage(option) {
        wx.onMenuShareAppMessage({
            title: option.title, // 
            desc: option.desc, // 
            link: option.link, // 
            imgUrl: option.imgUrl, // 
            success() {
                // 
                option.success()
            },
            cancel() {
                // 
                option.error()
            }
        })
    }
}
export default wxApi